import { useContext } from 'react';
import { Link, Outlet } from 'react-router-dom';
import { Context } from '../App';
import Traductor from './Traductor';
import "../styles/Header.css";

function Header () : JSX.Element {

    const { state } = useContext(Context);

    return (
        <>
            <header>
                <Link to ="/" id='title'>
                    <h1>M</h1>
                    <p id="name">Mateo Armijos</p>
                    <p id="subtitle">Web Developer</p>
                </Link>
                <nav>
                    <button className='links'>{state.lang === "en" ? "About Me" : "Sobre M"}</button>
                    <button className='links'>{state.lang === "en" ? "Skills" : "Habilidades"}</button>
                    <button className='links'>{state.lang === "en" ? "Work" : "Proyectos"}</button>
                </nav>
                <div id="external">
                    <div className="icons">
                        <a href="https://github.com/FMatharm" target="_blank" rel="noreferrer"><i className="fa-brands fa-github"></i></a>
                        <a href="https://ec.linkedin.com" target="_blank" rel="noreferrer"><i className="fa-brands fa-linkedin-in"></i></a>
                        <a href="https://twitter.com/FMatharm" target="_blank" rel="noreferrer"><i className="fa-brands fa-twitter"></i></a>
                    </div>
                    <Traductor />
                </div>
                <div id="responsive">
                    <span id="menu">
                        <i></i>
                        <i></i>
                        <i></i>
                    </span>
                </div>
            </header>
            <Outlet />
        </> 
    );
}

export default Header;